.banner {
  text-align: center; }

.banner__logo {
  height: 2em;
  background-color: #d5f5f7;
  border-radius: 50%;
  padding: 1px;
  vertical-align: middle;
  margin-left: 5px; }

.banner__header {
  height: calc(50vh + 175px);
  padding: 20px;
  color: white;
  padding-top: calc(50vh - 175px); }

.banner__title {
  display: inline-block;
  vertical-align: middle; }



